import './css/styles.css';

import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

import { getImagesByQuery, PER_PAGE } from './js/pixabay-api.js';
import {
  createGallery,
  clearGallery,
  showLoader,
  hideLoader,
  showLoadMoreButton,
  hideLoadMoreButton,
} from './js/render-functions.js';

const form = document.getElementById('search-form');
const loadMoreBtn = document.getElementById('load-more');
const submitBtn = form.querySelector('button[type="submit"]');

let currentQuery = '';
let currentPage = 1;

form.addEventListener('submit', onSearch);
loadMoreBtn.addEventListener('click', onLoadMore);

function setLoading(isLoading, { duringLoadMore = false } = {}) {
  submitBtn.disabled = isLoading;
  loadMoreBtn.disabled = isLoading;

  if (isLoading) showLoader();
  else hideLoader();

  if (!duringLoadMore && isLoading) hideLoadMoreButton();
}

function shouldShowMore(totalHits, page) {
  return page * PER_PAGE < totalHits;
}

function handleEndOfCollection() {
  hideLoadMoreButton();
  iziToast.info({
    title: '–ö—ñ–Ω–µ—Ü—å –∫–æ–ª–µ–∫—Ü—ñ—ó',
    message: "We're sorry, but you've reached the end of search results.",
    position: 'topRight',
  });
}

async function onSearch(evt) {
  evt.preventDefault();

  const query = form.elements['search-text'].value.trim();
  if (!query) {
    iziToast.warning({
      title: '–£–ø—Å',
      message: '–í–≤–µ–¥–∏ –∫–ª—é—á–æ–≤–µ —Å–ª–æ–≤–æ –ø–µ—Ä–µ–¥ –ø–æ—à—É–∫–æ–º.',
      position: 'topRight',
    });
    return;
  }

  currentQuery = query;
  currentPage = 1;
  hideLoadMoreButton();
  clearGallery();

  setLoading(true);
  try {
    const data = await getImagesByQuery(currentQuery, currentPage);
    const hits = data?.hits ?? [];

    if (hits.length === 0) {
      iziToast.info({
        title: '–ù—ñ—á–æ–≥–æ –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ',
        message:
          'Sorry, there are no images matching your search query. Please try again!',
        position: 'topRight',
      });
      return;
    }

    createGallery(hits);

    if (shouldShowMore(data.totalHits, currentPage)) {
      showLoadMoreButton();
    } else {
      handleEndOfCollection();
    }
  } catch {
    iziToast.error({
      title: '–ü–æ–º–∏–ª–∫–∞',
      message: '–ü—Ä–æ–±–ª–µ–º–∞ –∑ –º–µ—Ä–µ–∂–µ—é –∞–±–æ API. –°–ø—Ä–æ–±—É–π –ø—ñ–∑–Ω—ñ—à–µ.',
      position: 'topRight',
    });
  } finally {
    setLoading(false);
  }
}

async function onLoadMore() {
  currentPage += 1;
  setLoading(true, { duringLoadMore: true });

  try {
    const data = await getImagesByQuery(currentQuery, currentPage);
    const hits = data?.hits ?? [];
    if (hits.length === 0) {
      handleEndOfCollection();
      return;
    }

    createGallery(hits);

    if (!shouldShowMore(data.totalHits, currentPage)) {
      handleEndOfCollection();
    }

 
    const firstCard = document.querySelector('.gallery .card');
    if (firstCard) {
      const h = firstCard.getBoundingClientRect().height;
      window.scrollBy({ top: h * 2, behavior: 'smooth' });
    }
  } catch {
    iziToast.error({
      title: '–ü–æ–º–∏–ª–∫–∞',
      message: '–ù–µ –≤–¥–∞–ª–æ—Å—è –∑–∞–≤–∞–Ω—Ç–∞–∂–∏—Ç–∏ –Ω–∞—Å—Ç—É–ø–Ω—ñ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è.',
      position: 'topRight',
    });
  } finally {
    setLoading(false);
  }
}


